import { Component, ViewChild, ElementRef } from '@angular/core';
import { NavController, Content } from 'ionic-angular';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  specs: any[] = [];
  model = {
    name: '',
    email: '',
    subject: '',
    message: ''
  };
  year: number = new Date().getFullYear();
  @ViewChild('contact') contact: ElementRef;
  @ViewChild('vision') vision: ElementRef;
  @ViewChild('accueil') accueil: ElementRef;
  @ViewChild('speciality') speciality: ElementRef;
  @ViewChild('content') content: Content;
  constructor(public navCtrl: NavController) {
    this.specs = [
      {
        title: 'NUMERO COURT',
        subTitle: 'Paragraphe.Cliquez ici pour ajouter votre propre texte.'
      },
      {
        title: 'SOLUTION PAIEMENT',
        subTitle: 'Paragraphe.Cliquez ici pour ajouter votre propre texte.'
      },
      {
        title: 'USSD',
        subTitle: 'Paragraphe.Cliquez ici pour ajouter votre propre texte.'
      },
      {
        title: 'INTEGRATION',
        subTitle: 'Paragraphe.Cliquez ici pour ajouter votre propre texte.'
      }
    ];
  }
  onClick(id: string) {
    let element;
    if (id == 'accueil') element = this.accueil.nativeElement;
    else if (id == 'contact') element = this.contact.nativeElement;
    else if (id == 'vision') element = this.vision.nativeElement;
    else if (id == 'speciality') element = this.speciality.nativeElement;
    if (element) {
      this.content.scrollTo(0.0, element.offsetTop, 1000);
    }
  }

}



